.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #222222;
  color: #ffffff;
  padding: 1rem;
  position: relative;
  /* z-index: 1000; */
}

.navbar-logo a {
  color: #ffffff;
  text-decoration: none;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
}

.navbar-brand-logo {
  height: 30px;
  margin-right: 10px;
}

.navbar-menu {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.navbar-menu li {
  margin-left: 1rem;
}

.navbar-menu a {
  color: #ffffff;
  text-decoration: none;
  font-size: 1rem;
}

.navbar-menu a:hover {
  color: #dddddd;
}

.navbar-menu a.active {
  color: #ffd400;
  font-weight: bold;
}

.navbar-toggle-button {
  display: none;
  background: none;
  border: none;
  color: white;
  font-size: 1.5rem;
  cursor: pointer;
}

/* 모바일 화면 스타일 */
@media (max-width: 768px) {
  .navbar-toggle-button {
    display: block;
  }

  .navbar-menu {
    display: flex;
    position: absolute;
    top: 60px;
    left: 0;
    width: 100%;
    background-color: #222222;
    flex-direction: column;
    align-items: center;
    z-index: 999;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;

    /* 애니메이션을 위한 스타일 */
    max-height: 0;
    overflow: hidden;
    padding: 0;
    margin: 0;
    transition: max-height 0.3s ease-in-out, padding 0.3s ease-in-out;
  }

  .navbar-menu.mobile.active {
    max-height: 50vh; /* 메뉴가 펼쳐졌을 때의 최대 높이 */
    padding: 1rem 0;
  }

  .navbar-menu li {
    margin-left: 0; /* 모바일에서는 왼쪽 마진 제거 */
    margin: 1rem 0;
  }
}
