.image-gallery-container {
  padding: 10px;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  margin-bottom: 12px;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  position: relative;
  z-index: 1;
}

.image-gallery {
  display: flex;
  gap: 10px;
  margin-bottom: 30px;
  width: 100%;
  box-sizing: border-box;
}

.image-gallery-column {
  /* No styles needed here with flex gap */
}

@media (max-width: 1260px) {
  .image-gallery-container {
    max-width: 900px;
  }
}

@media (max-width: 960px) {
  .image-gallery-container {
    max-width: 100%;
  }
}

@media (max-width: 480px) {
  .image-gallery {
    gap: 8px;
  }
  
  .image-gallery-container {
    padding: 5px;
    width: 100%;
    max-width: 100%;
  }
}

.image-card {
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  transition: transform 0.2s;
  background: white;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  cursor: pointer;
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}

.image-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(0,0,0,0.2);
}

.image-wrapper {
  position: relative;
  background-color: #f5f5f5;
  width: 100%;
  /* aspect-ratio removed for masonry layout */
}

.new-badge {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: #ff4d4d;
  color: white;
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 700;
  z-index: 1;
}

.gallery-image {
  /* height auto for masonry */
  width: 100%;
  height: auto;
  display: block;
  object-fit: contain;
  object-position: center;
  
  /* 로드 효과를 위한 스타일 */
  opacity: 0;
  transform: scale(0.95);
  transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
}

.gallery-image.loaded {
  opacity: 1;
  transform: scale(1);
}

.image-info {
  padding: 10px;
  text-align: center;
  background: white;
  border-top: 1px solid #eee;
}

.image-info h3 {
  margin: 0 0 5px 0;
  color: #000;
  font-size: 1em;
  line-height: 1.2;
  word-break: keep-all;
}

.image-info p {
  margin: 3px 0;
  color: #666;
  font-size: 0.8em;
  line-height: 1.2;
  word-break: keep-all;
}

.no-images-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 2rem;
  width: 100%;
}

.no-images-message {
  font-size: 1.2rem;
  color: #888;
  text-align: center;
}

.team-name {
  font-size: 0.8em;
  color: grey;
}

.image-card .image-info .theme-type-info {
  font-size: 0.9em;
  font-weight: bold;
  color: #333;
}

.loading-indicator {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  width: 100%;
}

.loading-indicator::after {
  content: '...';
  font-size: 2rem;
  animation: dots 1.5s infinite;
}

@keyframes dots {
  0%, 20% {
    color: rgba(0,0,0,0);
    text-shadow:
      .25em 0 0 rgba(0,0,0,0),
      .5em 0 0 rgba(0,0,0,0);
  }
  40% {
    color: #888;
    text-shadow:
      .25em 0 0 rgba(0,0,0,0),
      .5em 0 0 rgba(0,0,0,0);
  }
  60% {
    text-shadow:
      .25em 0 0 #888,
      .5em 0 0 rgba(0,0,0,0);
  }
  80%, 100% {
    text-shadow:
      .25em 0 0 #888,
      .5em 0 0 #888;
  }
}